import React, { useState, useContext } from 'react';
import axios from 'axios';

import Store from '../../store';

import Header from './Header';
import Footer from './Footer';
import FriendsList from './FriendsList';
import Chats from './Chats';
import Settings from './Settings';

import './sidebar.scss';

export default () => {
  const { store } = useContext(Store);
  const [activeTab, setActiveTab] = useState('Contacts');
  const [activeFriend, setActiveFriend] = useState({});
  const [user, setUser] = useState({});
  const getUser = async () => {
    const { data: users } = await axios.get(`${store.API_URL}/users`, {
      headers: {
        Authorization: localStorage.getItem('TOKEN')
      }
    });
    setUser(users[0]);
  }
  if (!Object.keys(user).length) getUser();

  const logout = () => {
    localStorage.clear();
    window.location.reload();
  };

  return <div className="sidebar">
    <Header activeTab={activeTab}/>
    <div className="sidebar-dynamic-content">
      {
        activeTab === 'Contacts' && <FriendsList friends={user.friends} activeFriend={activeFriend} setActiveFriend={setActiveFriend} />
      }
      {
        activeTab === 'Chats' && <Chats friend={activeFriend}/>
      }
      {
        activeTab === 'Settings' && <Settings logout={logout}/>
      }
    </div>
    <Footer setActiveTab={setActiveTab} activeTab={activeTab} />
  </div>
}
