import React, { useContext } from 'react';

import Store from '../../store';

export default ({ friends = [], activeFriend, setActiveFriend }) => {
  const { store, setContext } = useContext(Store);
  return (
    <div className="friend-list">
    {
      friends.map((user, id) => <div className="user" key={id} onClick={() => {
        setActiveFriend(user);
        setContext({
          ...store,
          activeFriend: user
        });
      }}>
        <div className="profile-pic">
          <span>{`${user.firstName[0]} ${user.lastName[0]}`}</span>
        </div>
        <div className="user-detail">
          <span className="username">{`${user.firstName} ${user.lastName}`}</span>
          <span className="last-seen">Seen a long time ago</span>
        </div>
        <div className="online-status">
          {
            id % 2 === 0 ?
              <div className="online"></div> :
              <div className="offline"></div>
          }
        </div>
      </div>)
    }
  </div>
  );
}

